___CSS_LOADER_EXPORT___.push([module.id, `.dashboard {
  display: flex;
  justify-content: space-between;
}

.panel {
  background-color: #333;
  padding: 20px;
  border-radius: 10px;
  color: white;
}

.camera-feed {
  flex: 1;
  margin-right: 20px;
}

.video-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.video-container video {
  width: 100%;
  height: 150px; /* 각 비디오의 높이를 설정 */
  object-fit: cover; /* 비디오가 컨테이너에 맞게 잘리도록 설정 */
  border-radius: 5px;
}

.notification-panel {
  flex: 1;
  margin-right: 20px;
}

.stats-section {
  flex: 1;
}

.stats {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.stat {
  background-color: #444;
  padding: 20px;
  border-radius: 5px;
  text-align: center;
}

.stat-title {
  font-size: 16px;
}

.stat-value {
  font-size: 24px;
  font-weight: bold;
}
